<template>
  <div class="center">
    <div class="center_breadcrumb">
      <crumb style="padding: 0 5px;">
        <div class="new--btn" slot="right" @click="openAdd">+ 新建</div>
      </crumb>
    </div>
    <div class="center_content">
      <div class="center_content_search">
        <div class="center_content_search_left">
          <div class="search-input-suffix" style="margin-right: 15px;">
            <label>所属专业：</label>
            <el-select v-model="specialtyID" placeholder="请选择" @change="changespecialtyID">
              <el-option
                v-for="item in roleOptions2"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </div>
          <div class="search-input-suffix">
            <label>版本搜索：</label>
            <el-input
              class="search"
              @keyup.enter.native="search"
              v-model="page.keywords"
              placeholder="请输入搜索关键词"
            >
              <!-- <i slot="suffix" class="el-icon-search" @click="search" style="line-height: 30px;"></i> -->
            </el-input>&nbsp;&nbsp;
            <!-- <el-button @click="search" type="primary">搜索</el-button> -->
          </div>
          <div class="search-input-suffix" style="margin: 0 0 0 20px">
            <div class="search--btn"  @click="search">搜索</div>
          </div>
        </div>
      </div>
      <div class="table-container">
        <el-table :data="tableData" stripe style="width: 100%">
          <el-table-column prop="name" label="版本名称" align="center"></el-table-column>
          <el-table-column prop="grade" label="适用于哪年入校学生"></el-table-column>
          <el-table-column prop="specialtyVal" label="专业名称"></el-table-column>
          <el-table-column prop="isEnableVal" label="是否启用" align="center">
            <!-- <template slot-scope="scope">
                            <el-switch v-model="scope.row.isEnable" disabled active-color="#0079fe" inactive-color="#eee">
                            </el-switch>
            </template>-->
          </el-table-column>
          <el-table-column prop="isDefaultVal" label="是否默认版本" align="center">
            <!-- <template slot-scope="scope">
                            <el-switch v-model="scope.row.isDefault" disabled active-color="#0079fe" inactive-color="#eee">
                            </el-switch>
            </template>-->
          </el-table-column>
          <el-table-column label="操作" width="175">
                <template slot-scope="scope">                  
                    <div class="operate-button distance">
                        <SvgIcon icon-class="iconbianji1"></SvgIcon> 
                        <el-button
                        @click.native.prevent="editRow(scope.row)"
                        type="text"
                        size="small">
                        编辑
                        </el-button>
                    </div>
                    <div class="operate-button">
                        <SvgIcon icon-class="iconshanchu"></SvgIcon>
                        <el-button
                        @click.native.prevent="deleteRow(scope.row)"
                        type="text"
                        size="small"
                        style="color: #ff5858">
                        删除
                        </el-button>
                    </div>                    
                </template>
          </el-table-column>
        </el-table>
        <div class="center_pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            background
            :current-page="page.page"
            :page-sizes="[15,30,40,50]"
            :page-size="page.rows"
            layout="prev, pager, next, jumper, sizes, total"
            :total="data.total"
          ></el-pagination>
        </div>
      </div>
    </div>
    <!-- 新增编辑 -->
    <el-dialog
      :title="dialogTitle"
      :visible.sync="userDialogFormVisible"
      :before-close="handleDialogClose"
      class="set-el-dialog"
      :close-on-click-modal="false"
    >
      <el-form :model="form" :rules="rules" ref="form" label-width="120px">
        <el-form-item label="培养方案名称" prop="name">
          <el-input v-model="form.name" placeholder></el-input>
        </el-form-item>
        <el-row>
          <el-col :span="12">
            <el-form-item label="所属专业" prop="specialtyId">
              <el-select v-model="form.specialtyId" :disabled="!isAdd" placeholder="请选择">
                <el-option
                  v-for="item in specialtyArr"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="适用年级" prop="grade">
              <el-select v-model="form.grade" multiple placeholder="请选择">
                <el-option
                  v-for="item in gradeOptions"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <!-- <el-col :span="6">
            <el-form-item label="是否启用" prop="isEnable">
              <el-checkbox v-model="form.isEnable"></el-checkbox>
            </el-form-item>
          </el-col> -->
          <el-col :span="6">
            <el-form-item label="是否默认版本" prop="isDefault">
              <el-checkbox v-model="form.isDefault"></el-checkbox>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item label="目标达成值" prop="minimum">
          <el-input-number v-model="form.minimum" controls-position="right"></el-input-number>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel('form')">取 消</el-button>
        <el-button type="primary" @click="create('form')" :disabled="isSubmit">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 复制方案 -->
    <el-dialog
      title="复制培养方案"
      :visible.sync="copyDialogFormVisible"
      class="set-el-dialog"
      :close-on-click-modal="false"
    >
      <el-form :model="copyForm" :rules="copyRules" ref="copyForm" label-width="120px">
        <el-form-item label="原版本" prop="sourceVersionID">
          <el-select v-model="copyForm.sourceVersionID" @change="versionSelect" placeholder="请选择">
            <el-option
              v-for="item in copyOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="目标版本" prop="targetVersionID">
          <el-select v-model="copyForm.targetVersionID" placeholder="请选择">
            <el-option
              v-for="item in copyOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="复制内容" prop>
          <el-checkbox-group v-model="flagSelect">
            <el-checkbox
              v-for="(vo, index) in copyData"
              :label="vo.keyName"
              :key="index"
              :checked="vo.checked"
            >
              {{vo.label}}(
              <label style="color:#3366ff">{{vo.status === 0 ? '未建设' : '已建设'}}</label>)
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="copyCancel('copyForm')">取 消</el-button>
        <el-button type="primary" @click="copyCreate('copyForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import trainingProgramApi from "@/api/trainingProgramApi";
export default {
  name: "CultureProgramVersion",
  data() {
    const checkMinimum = (rule, value, callback) => {
      if (value === 0 || !value) {
        return callback(new Error("请输入目标达成值"));
      }
      if (value < 0 || value > 1) {
        return callback(new Error("目标达成值范围0-1之间"));
      } else {
        callback();
      }
    };
    const checkTargetVersion = (rule, value, callback) => {
      if (!this.copyForm.targetVersionID) {
        return callback(new Error("请选择目标版本"));
      }
      if (this.copyForm.targetVersionID === this.copyForm.sourceVersionID) {
        return callback(new Error("原版本与目标版本重复"));
      } else {
        callback();
      }
    };
    return {
      roleOptions2: [],
      specialtyID: "",
      pageTitle: "培养方案版本",
      data: {},
      tableData: [],
      isAdd: true,
      isSubmit:false,
      userDialogFormVisible: false,
      copyDialogFormVisible: false,
      gradeOptions: [],
      page: {
        keywords: "",
        page: 1,
        rows: 15,
        specialtyId: "",
        academyId: localStorage.getItem("academyId")
      },
      form: {
        name: "",
        grade: [],
        isEnable: false,
        isDefault: false,
        minimum: null,
        id: undefined,
        specialtyId: ""
      },
      rules: {
        name: [
          {
            required: true,
            message: "请输入版本名称",
            trigger: "blur"
          }
        ],
        grade: [
          {
            required: true,
            message: "请选择适用年级",
            trigger: "blur"
          }
        ],
        specialtyId: [
          {
            required: true,
            message: "请选择所属专业",
            trigger: "blur"
          }
        ],
        minimum: [
          {
            required: true,
            validator: checkMinimum,
            trigger: "blur"
          }
        ]
      },
      copyData: [
        {
          keyName: "goal",
          label: "培养目标",
          status: null,
          checked: false
        },
        {
          keyName: "claim",
          label: "毕业要求",
          status: null,
          checked: false
        },
        {
          keyName: "course",
          label: "课程体系",
          status: null,
          checked: false
        }
      ],
      copyOptions: [],
      flagSelect: [],
      copyForm: {
        sourceVersionID: "",
        targetVersionID: "",
        flag: []
      },
      copyRules: {
        sourceVersionID: [
          {
            required: true,
            message: "请选择原版本",
            trigger: "blur"
          }
        ],
        targetVersionID: [
          {
            required: true,
            validator: checkTargetVersion,
            trigger: "blur"
          }
        ]
      },
      specialtyArr: [],
      gradesArr: []
    };
  },
  created() {},
  mounted() {
    this.initData();
    this.versionAll();
    this.versionGrades();
    this.specialty();
  },
  computed: {
    dialogTitle() {
      return this.isAdd ? "添加培养方案版本" : "编辑培养方案版本";
    }
  },
  methods: {
    initData() {
      const url = trainingProgramApi.versionList;
      this.$post(url, this.page)
        .then(res => {
          if (res.status === 1) {
            this.data = res;
            this.tableData = res.data;
            this.tableData.forEach(vo => {
              //是否启用(0:否 1:是)
              vo.isEnable = vo.isEnable === 0 ? false : true;
              //是否默认版本(0:否 1:是)
              vo.isDefault = vo.isDefault === 0 ? false : true;
            });
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    versionAll() {
      const url = trainingProgramApi.versionAll;
      this.$get(url)
        .then(res => {
          if (res.status === 1) {
            this.copyOptions = res.data;
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //所有学生入学年份
    versionGrades() {
      const url = trainingProgramApi.versionGrades;
      this.$get(url)
        .then(res => {
          if (res.status === 1) {
            this.gradeOptions = res.data;
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //  所有专业  /basic/specialty/all/{academyID}
    specialty() {
      let academyId = localStorage.getItem("academyId");
      let specialtyId = localStorage.getItem("specialtyId");
      this.$get(
        `/basic/specialty/all/${specialtyId == "0" ? academyId : specialtyId}`
      ).then(res => {
        this.specialtyArr = res.data;
        this.roleOptions2 = res.data;
        // this.specialtyArr.unshift({id:'',name:'全部 '})
      });
    },
    changespecialtyID(val) {
      this.page.specialtyId = val;
      this.initData();
    },
    headInfo(item) {
      this.pageTitle = item[1];
    },
    search() {
      this.resetTemp();
      this.initData();
    },
    handleSizeChange(val) {
      this.page.rows = val;
      this.initData();
    },
    handleCurrentChange(obj) {
      this.page.page = obj;
      this.initData();
    },
    openAdd() {
      this.userDialogFormVisible = true;
      this.form = {
        name: "",
        grade: [],
        isEnable: false,
        isDefault: false,
        minimum: null,
        id: undefined,
        specialtyId: "",
        academyId: localStorage.getItem("academyId")
      };
      this.isAdd = true;
      this.isSubmit=false;
      this.$nextTick(() => {
        this.$refs["form"].resetFields();
      });
    },
    deleteRow(row) {
      this.$confirm("确定删除该版本?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.userDel(row.id);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    editRow(row) {
      const arr = row.grade.split(",") || [];
      const newArr = arr.map(item => {
        return +item;
      });
      const obj = {
        name: row.name,
        grade: newArr,
        isEnable: row.isEnable,
        isDefault: row.isDefault,
        minimum: row.minimum,
        id: row.id,
        specialtyId: row.specialtyId
      };
      this.form = obj;
      this.userDialogFormVisible = true;
      this.isAdd = false;
      this.isSubmit=false;
    },
    create(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const newForm = JSON.parse(JSON.stringify(this.form));
          const formObj = {
            name: newForm.name,
            grade: newForm.grade.join(","),
            isEnable: newForm.isEnable ? 1 : 0,
            isDefault: newForm.isDefault ? 1 : 0,
            minimum: newForm.minimum,
            id: newForm.id,
            specialtyId: newForm.specialtyId,
            academyId: localStorage.getItem("academyId")
          };
           this.isSubmit=true;
          if (this.isAdd) {
            this.userAdd(formObj);
          } else {
            this.userEdit(formObj);
          }
        }
      });
    },
    cancel(formName) {
      this.userDialogFormVisible = false;
      this.$refs[formName].resetFields();
    },
    handleDialogClose() {
      this.$refs["form"].resetFields();
      this.userDialogFormVisible = false;
    },
    userAdd(formData) {
      const url = trainingProgramApi.versionAdd;
      this.$post(url, formData)
        .then(res => {
          if (res.status === 1) {
            this.isSubmit=true;
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.userDialogFormVisible = false;
            this.resetTemp();
            this.initData();
          } else {
             this.isSubmit=false;
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
           this.isSubmit=false;
          console.log(err);
        });
    },
    userDel(id) {
      const url = trainingProgramApi.versionDel({
        id: id
      });
      this.$deletes(url)
        .then(res => {
          if (res.status === 1) {
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.initData();
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    userEdit(formData) {
      const url = trainingProgramApi.versionEdit;
      this.$put(url, formData)
        .then(res => {
          if (res.status === 1) {
             this.isSubmit=true;
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.userDialogFormVisible = false;
            this.initData();
          } else {
            this.isSubmit=false;
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          this.isSubmit=false;
          console.log(err);
        });
    },
    openCopy() {
      this.copyDialogFormVisible = true;
      this.flagSelect = [];
      this.copyForm = {
        sourceVersionID: "",
        targetVersionID: "",
        flag: []
      };
      this.$nextTick(() => {
        this.$refs["copyForm"].resetFields();
      });
    },
    versionSelect() {
      this.versionContent(this.copyForm.sourceVersionID);
    },
    copyCreate(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let arr = [];
          if (this.flagSelect.length === 0) {
            arr = [];
          } else {
            //复制内容（0:培养目标 1:毕业要求 2:课程）
            this.flagSelect.forEach(item => {
              if (item === "goal") {
                arr.push(0);
              } else if (item === "claim") {
                arr.push(1);
              } else if (item === "course") {
                arr.push(2);
              }
            });
          }
          this.copyForm.flag = arr;
          this.versionCopy();
        }
      });
    },
    copyCancel(formName) {
      this.copyDialogFormVisible = false;
      this.$refs[formName].resetFields();
    },
    versionContent(id) {
      const url = trainingProgramApi.versionContent({
        versionId: id
      });
      this.$get(url)
        .then(res => {
          if (res.status === 1) {
            this.copyData[0].status = res.data.goal;
            this.copyData[1].status = res.data.claim;
            this.copyData[2].status = res.data.course;
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    versionCopy() {
      const url = trainingProgramApi.versionCopy;
      this.$post(url, this.copyForm)
        .then(res => {
          if (res.status === 1) {
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.copyDialogFormVisible = false;
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    resetTemp() {
      this.page.page = 1;
      this.page.rows = 15;
    }
  }
};
</script>

<style lang="scss">
@import "../../styles/center.scss";
</style>



